// Ширина 
$min_width: 320px;
$max_width: 1920px;
$body_width: 1440px;
// ===========
// Цвета
$dark100: rgba(31, 32, 65, 1);
$dark75: rgba(31, 32, 65, 0.75);
$dark50: rgba(31, 32, 65, 0.5);
$dark25: rgba(31, 32, 65, 0.25);
$dark10: rgba(31, 32, 65, 0.1);
$dark05: rgba(31, 32, 65, 0.05);
$purple: rgba(188, 156, 255, 1);
$green: rgba(111, 207, 151, 1);
// =============
// Шаблоны
%text_field {
    padding-left: 15px;
    border: 1px solid $dark25;
    width: 320px;
    height: 44px;
    border-radius: 4px;
}

%hover_focus {
    border: 1px solid $dark50;
    border-radius: 4px;
}

%radio-buttons {
    width: 220.74px;
    h3 {
        margin-bottom: 13px;
    }
    .radio-block {
        display: grid;
        grid-template-columns: auto auto;
        column-gap: 20px;
        span {
            display: grid;
            grid-template-columns: auto auto;
            position: relative;
            input[type='radio'] {
                width: 20px;
                height: 20px;
                opacity: 0;
                margin: auto;
                z-index: 2;
            }
            input[type='radio']:checked+ ::before {
                position: absolute;
                content: '';
                width: 20px;
                height: 20px;
                top: 3px;
                left: 0;
                background: url('./../image/Rectangle 2.1.svg') no-repeat;
                background-position: 3px 3px;
                border: 1px solid $purple;
                border-radius: 10px;
                // z-index: 0;
            }
            &::after {
                position: absolute;
                content: "";
                width: 20px;
                height: 20px;
                border: 1px solid $dark25;
                border-radius: 10px;
                top: 3px;
                left: 0;
                z-index: 1;
            }
            input[type='radio']:checked+label {
                color: $dark75;
            }
            label {
                margin-left: 10px;
                vertical-align: middle;
                color: $dark50;
            }
        }
    }
}

// ===========
// Миксины
@mixin fontFamily_Montserrat ($size,
$weight,
$height,
$align,
$col) {
    font-family: Montserrat;
    font-size: $size;
    font-style: normal;
    font-weight: $weight;
    line-height: $height;
    letter-spacing: 0em;
    text-align: $align;
    color: $col;
}

@mixin Colors_Block ($b_col) {
    width: 70px;
    height: 70px;
    border-radius: 6px;
    background-color: $b_col;
}

// ===========