.colors {
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 40px;
    .colors-block {
        display: grid;
        grid-template-rows: repeat(7, 1fr);
        row-gap: 20px;
        justify-items: center;
        align-items: center;
        #dark100 {
            @include Colors_Block ($dark100);
        }
        #dark75 {
            @include Colors_Block ($dark75);
        }
        #dark50 {
            @include Colors_Block ($dark50);
        }
        #dark25 {
            @include Colors_Block ($dark25);
        }
        #dark05 {
            @include Colors_Block ($dark05);
        }
        #purple {
            @include Colors_Block ($purple);
        }
        #green {
            @include Colors_Block ($green);
        }
    }
    .colors-content {
        display: grid;
        grid-template-rows: repeat(7, 1fr);
        row-gap: 20px;
        p {
            margin: auto 0;
            @include fontFamily_Montserrat (19px, 700, 27.79px, left, $dark100);
            span {
                @include fontFamily_Montserrat (19px, 400, 27.79px, left, $dark100);
            }
        }
    }
}