.multi-range-slider {
    width: 266px;
    height: 47.44px;
    position: relative;
    h3 {
        position: absolute;
    }
    #right-label {
        position: absolute;
        right: 0;
        // margin: -3px 0 0 0;
        letter-spacing: 0em;
        line-height: 14.63px;
        font-size: 12px;
        text-align: right;
        color: $dark50;
    }
    .range-slider {
        position: relative;
        input[type="range"] {
            position: absolute;
            top: 30px;
            pointer-events: none;
            appearance: none;
            z-index: 2;
            height: 6px;
            width: 100%;
            opacity: 0;
        }
        input[type="range"]::-webkit-slider-thumb {
            pointer-events: all;
            cursor: pointer;
            border-radius: 0;
            border: 0 none;
        }
        .slider {
            position: relative;
            top: 30px;
            z-index: 1;
            height: 6px;
            .range {
                position: absolute;
                z-index: 2;
                left: 25%;
                right: 25%;
                top: 0;
                bottom: 0;
                border-radius: 10px;
                background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
            }
            .track {
                position: absolute;
                height: 6px;
                width: 100%;
                z-index: 1;
                border: 1px solid $dark25;
                border-radius: 5px;
                background-color: #ffffff;
            }
            .thumb {
                position: absolute;
                z-index: 3;
                width: 12px;
                height: 12px;
                top: -3px;
                background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
                border: 2px solid #fff;
                border-radius: 50%;
                // .left {
                //     transform: translate(-1px, -3px);
                // }
                // .right {
                //     transform: translate(1px, -3px);
                // }
            }
        }
    }
}